import "./App.css";
import React from "react";
import { useEffect, useState } from "react";
import ReactConfetti from "react-confetti";

const Confetti = ({ handleShow, isActive }) => {
  const [windowDimension, setDimension] = useState({
    width: window.innerWidth,
    height: window.innerHeight,
  });
  const [Btn, setBtn] = useState(false);

  const detectSize = () => {
    setDimension({
      width: window.innerWidth,
      height: window.innerHeight,
    });
  };
  useEffect(() => {
    window.addEventListener("resize", detectSize);
    return () => {
      window.removeEventListener("resize", detectSize);
    };
  }, [windowDimension]);

  return (
    <>
      <button
        className={isActive ? "hidden" : "button-4"}
        onClick={() => {
          setBtn(!Btn);
          handleShow();
        }}
      >
        попиздовать с бывшим на стражи галактики и вкусняшки
      </button>
      {Btn && (
        <ReactConfetti
          width={windowDimension.width}
          height={windowDimension.height}
          tweenDuration={1000}
        />
      )}
    </>
  );
};

function App() {
  const [isActive, setIsActive] = useState(false);
  const runAwayBtn = (e) => {
    e.target.style.left = `${Math.ceil(Math.random() * 90)}%`;
    e.target.style.top = `${Math.ceil(Math.random() * 90)}%`;
    e.target.style.rigth = `${Math.ceil(Math.random() * 90)}%`;
    e.target.style.down = `${Math.ceil(Math.random() * 90)}%`;
  };
  const handleShow = (event) => {
    setIsActive((current) => !current);
  };

  return (
    <div className="App">
      <h1>С днем рождедия ВААААААССЯЯЯЯЯЯЯЯ!!!!!!!!!!!</h1>
      <h2 className={isActive ? "hidden" : ""}>Выбери подарок</h2>
      <h3 className={isActive ? "show" : "hidden"}>
        Правильный выбор, желаю чтобы ты в жизни всегда могла выбирать самое
        лучшее и ничего тебе этом не мешало
      </h3>
      <div className="buttonContainer">
        <button
          onMouseEnter={runAwayBtn}
          className={isActive ? "hidden" : "button-1"}
        >
          Iphone 14 pro max
        </button>
        <button
          onMouseEnter={runAwayBtn}
          className={isActive ? "hidden" : "button-2"}
        >
          Acura Integra
        </button>
        <button
          onMouseEnter={runAwayBtn}
          className={isActive ? "hidden" : "button-3"}
        >
          поездка во флоренцию
        </button>
        <Confetti handleShow={handleShow} isActive={isActive} />
      </div>
    </div>
  );
}

export default App;
